<template>
  <!-- 订单发货 -->
  <el-dialog :visible="visible" title="订单发货" :before-close="handleClose" width="900px" >
    <div style="padding:15px"  v-loading="loading">
      <h2 class="cms-segmentation" style="padding-left:15px;">第一步:确认收货信息</h2>
      <table class="order-table">
        <tr class="order-header">
          <td style="border:1px solid #e0e0e0;">
            <span style="margin-left:20px">订单编号：</span>
            <span style="margin-right: 50px;">{{orderInfo.orderCode}}</span>
            <span>下单时间:</span>
            <span style="margin-right: 60px;">{{$moment(orderInfo.orderTime)}}</span>
          </td>
        </tr>
        <tbody>
          <tr v-for="(product) in orderInfo.items" :key="product.id">
            <td class="product-info" style="border-right: 1px solid #e0e0e0;">
              <div class="product__inner">
                <img
                  class="product-img"
                  :src="$cms.filterUrl(product.productInfo.coverImageUrl)"
                  v-if="product.productInfo"
                >
                <div class="product-title">
                  <h3
                    class="title-h3"
                    v-if="product.productInfo"
                  >{{product.productInfo.productName}}</h3>
                  <p
                    class="title-p"
                    style="margin-top:10px;"
                    v-if="product.productInfo"
                  >{{showAttr(product.productInfo.skuAttr)}}</p>
                  <div class="overflow">
                    <span class="de-price">￥{{$cms.float(product.payAmount)}}</span>
                    <span class="de-count">x{{product.productCount}}</span>
                  </div>
                </div>
              </div>
            </td>
          </tr>
          <tr v-if="orderInfo.orderExt">
            <td class="product-info" style="border-right: 1px solid #e0e0e0;">
              <div class="de-addr">
                <label for class="de-label">买家收货地址：</label>
                <span class="de-info">
                  {{orderInfo.orderExt.customerAddress}},
                  {{orderInfo.orderExt.customerName}}({{orderInfo.orderExt.customerTel}}),
                  {{orderInfo.orderExt.customerAreaCode}}
                </span>
              </div>
              <div class="de-cust">
                <label for class="de-label">买家备注：</label>
                <span class="de-info">{{orderInfo.orderExt.customerTxt}}</span>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <h2 class="cms-segmentation" style="padding-left:15px;">第二步:选择物流信息</h2>
      <el-form :model="deliveryForm" ref="form" label-width="120px" :rules="rules">
        <el-form-item label="物流公司" prop="deliveryCompanyId">
          <el-select v-model="deliveryForm.deliveryCompanyId">
            <el-option
              :label="item.cnName"
              :value="item.id"
              v-for="item in companyList"
              :key="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物流单号" prop="expressNum">
          <el-input maxlength="14" v-model="deliveryForm.expressNum"></el-input>
        </el-form-item>
        <el-form-item label="选择发货地址" prop="addressId">
          <el-select v-model="deliveryForm.addressId" style="width:500px">
            <el-option
              :label="item.customerName+'('+item.tel+')      ' +item.provinceName +item.cityName+item.areaName +item.address"
              :value="item.id"
              v-for="item in addressList"
              :key="item.id"
            >
              <div>
                <span style="float: left;margin-right:30px">{{item.customerName}} {{item.tel}}</span>
                <span
                  style="float: right"
                >{{item.provinceName}} {{item.cityName}} {{item.areaName}}{{item.address}}</span>
              </div>
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="deliverySave">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import {
  orderDelivery,
  logisticscompanyList,
  storeAddressPage
} from './../../../api/shop/order.js'
import rules from '@/rules'
export default {
  data() {
    return {
      visible: false,
      loading: false,
      orderInfo: [], // 修改价格的集合
      deliveryForm: {
        orderId: '',
        deliveryCompanyId: '',
        expressNum: '',
        addressId: ''
      },
      companyList: [],
      addressList: [],
      rules: {
        deliveryCompanyId: [rules.required('此项必填', 'change')],
        expressNum: [rules.required()],
        addressId: [rules.required()]
      }
    }
  },
  methods: {
    setDelivery(data) {
      this.orderInfo = data
      this.deliveryForm.orderId = data.id
      this.visible = true
    },
    deliverySave() {
      // 提交订单修改信息
      this.$refs.form.validate(valid => {
        if (valid) {
          this.loading = true
          orderDelivery(this.deliveryForm).then(res => {
            this.loading = false
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.visible = false
              this.handleClose()
              this.$emit('change', true)
            } else {
              this.$errorMessage(res.message)
              this.handleClose()
              this.$emit('change', false)
            }
          })
        }
      })
    },
    getLogisticscompanyList() {
      logisticscompanyList().then(res => {
        if (res.code === 200) {
          this.companyList = res.data
        }
      })
    },
    getAddress() {
      storeAddressPage({ addressType: 1 }).then(res => {
        if (res.code === 200) {
          this.addressList = res.data
          res.data.forEach(v => {
            if (v.isDefault) {
              this.deliveryForm.addressId = v.id
            }
          })
        }
      })
    },
    showAttr(data) {
      // 转换json字符串属性
      if (data === '') {
        return ''
      } else {
        const arr = JSON.parse(data)
        let str = ''
        arr.forEach(v => {
          const tmp = v.name + ':' + v.value + '  '
          str = str + tmp
        })
        return str
      }
    },
    handleClose() {
      this.visible = false
      this.deliveryForm.expressNum = ''
      this.deliveryForm.deliveryCompanyId = ''
      this.$refs.form.clearValidate()
      this.$refs.form.resetFields()
    }
  },
  created() {
    this.getAddress()
    this.getLogisticscompanyList()
  }
}
</script>

<style>
</style>



