<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>显示店铺订单</li>
      <li>点击订单号即可进入详情页面</li>
    </cms-prompt>

    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>订单号</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.orderNum"></el-input>
      </div>
      <div class="query-group">
        <label>买家名称</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.memberName"></el-input>
      </div>
      <div class="query-group">
        <label>下单时间</label>
        <cms-picker v-model="timer" @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
        <label>支付方式</label>
        <el-select v-model="queryParams.payMode" @change="query">
          <el-option label="全部" :value="''"></el-option>
          <el-option label="支付宝" :value="1"></el-option>
          <el-option label="微信" :value="2"></el-option>
          <el-option label="银联" :value="3"></el-option>
          <el-option label="余额" :value="4"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 数据列表 -->
    <table class="order-table" v-loading="loading">
      <tr>
        <th colspan="6">订单详情</th>
        <th>收货人</th>
        <th>实收款</th>
        <th>订单状态</th>
        <th>操作选项</th>
      </tr>
      <tbody v-for="(item,index) in tableData" :key="index" >
        <div style="width:100%;height:25px;background:#fff;"></div>
        <!-- 头部 -->
        <tr class="order-header">
          <td colspan="11" style="border:1px solid #e0e0e0;">
            <span style="margin-left:20px">订单编号：</span>
            <span style="margin-right: 50px;">{{item.orderCode}}</span>
            <span>下单时间:</span>
            <span style="margin-right: 60px;">{{$moment(item.orderTime)}}</span>
          </td>
        </tr>
        <tr v-for="(product,index2) in item.items" :key="product.id">
          
            <td class="product-info" colspan="3">
            <!-- 商品信息 -->
            <div class="product__inner" v-if="item.items&&item.items.length>0">
              <img
                class="product-img"
                :src="$cms.filterUrl(product.productInfo.coverImageUrl)"
                v-if="product.productInfo"
              >
              <div class="product-title">
                <h3 class="title-h3" v-if="product.productInfo">{{product.productInfo.productName}}</h3>
                <p
                  class="title-p"
                  style="margin-top:10px;"
                  v-if="product.productInfo"
                >{{showAttr(product.productInfo.skuAttr)}}</p>
              </div>
            </div>
          </td>
          <td class="order-count">
            <!-- 价格 -->
            <span v-if="item.items&&item.items.length>0">￥{{$cms.float(product.productPrice)}}</span>
          </td>
          <td class="order-count">
            <!-- 数量 -->
            <span v-if="item.items&&item.items.length>0">x {{product.productCount}}</span>
          </td>
          <td class="order-count">
            <!-- 退换货状态 -->
            <span v-if="item.newRefund">{{item.newRefund.status|refund}}</span>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-user">
            <span v-if="item.member">
              <i class="iconfont icon-yonghu" style="position: relative;top: 2px;"></i>
              {{item.member.nickname||item.member.username}}
            </span>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="tr-price">
            <p class="await">￥{{$cms.float(item.orderPayAmount)}}</p>
            <p style="color:#999999;margin: 10px 0 0 0">(含运费：￥{{$cms.float(item.orderFreight)}})</p>
            <p>
              <i class="iconfont" style="color:#00C700;margin-right:5px"></i>
              {{item.payMode |payMode}}
            </p>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-status">
            <div class="await cell" v-if="item.status">
              <span v-if="item.status==1" class="info">待付款</span>
              <span v-if="item.status==2" class="info">待发货</span>
              <span v-if="item.status==3" class="info">待收货</span>
              <span v-if="item.status==4" class="success">已完成</span>
              <span v-if="item.status==5" class="error">已取消</span>
              <span v-if="item.status==6" class="error">超时交易关闭</span>
            </div>
            <p
              style="color:#4196E4;margin: 10px 0 10px 0; cursor: pointer;"
              class="link"
              @click="$routerLink('/orderManger/view','view',{id:item.id})"
            >订单详情</p>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-operation">
            <div class="mb-10">
              <cms-button
                size="small"
                type="info"
                slot="reference"
                style="width:110px"
                @click="printOrder(item.id)"
              >打印运单</cms-button>
            </div>
            <div class="mb-10">
              <!-- 订单是否允许发货条件限制（条件1和2中满足任意条件即进行发货操作）
              1.订单中商品的售后服务单状态为已完成、买家已取消
              2.订单中商品的售后服务单状态为卖家拒绝且服务单对应的申请平台记录状态为确认不通过 -->
              <cms-button
                size="small"
                type="info"
                slot="reference"
                style="width:110px"
                @click="deliveryOrder(item)"
                v-if="!item.newRefund || (item.newRefund&&((item.newRefund.status == 8 || item.newRefund.status == 9 )
                || (item.newRefund.status == 2 && item.newRefund.lastIntervent && item.newRefund.lastIntervent.platformStatus === 2)))"
              >发&nbsp;&nbsp;货</cms-button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <cms-pagination @change="getPage" :total="total" :size="5"></cms-pagination>
    <delivery ref="delivery" @change="updateList"></delivery>
    <print-page ref="print" style="display:none"></print-page>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { orderPage, cancelOrder } from '@/api/shop/order.js'
import delivery from './delivery.vue'
import printPage from '../order/shipBill.vue'
var defaultData = {
  page: 1,
  size: 5,
  status: '2',
  payMode: '',
  orderNum: '',
  orderTimeBegin: '',
  orderTimeEnd: '',
  memberName: ''
}
export default {
  name: 'orderList',
  mixins: [list],
  components: {
    delivery,
    printPage
  },
  data() {
    return {
      timer: [],
      activeStatus: '',
      queryParams: Object.assign({}, defaultData)
    }
  },
  filters: {
    payMode(value) {
      if (value === '') {
        return ''
      } else {
        const payMode = ['', '支付宝', '微信', '银联', '余额']
        return payMode[value]
      }
    },
    refund(value) {
      const statusMode = [
        '待卖家确认',
        '退款中',
        '卖家拒绝',
        '',
        '待买家发货',
        '待卖家收货',
        '待买家收货',
        '待卖家发货',
        '已完成',
        '买家已取消'
      ]
      return statusMode[value]
    }
  },
  methods: {
    updateList(status) {
      if (status) {
        this.query()
      }
    },
    deliveryOrder(item) {
      // 发货操作
      this.$refs.delivery.setDelivery(item)
    },
    cancelOrder(orderId) {
      this.$confirm('是否确认取消该订单', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(res => {
          cancelOrder({ orderId: orderId }).then(res => {
            if (res.code === 200) {
              this.$successMessage('取消成功')
              this.query()
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    printOrder(id) {
      this.$refs.print.getOrderInfo(id).then(res => {
        var printStr = res.innerHTML
        let printUrl = ''
        if (process.env.NODE_ENV === 'production') {
          const path = location.pathname.substring(
            0,
            location.pathname.lastIndexOf('/')
          )
          printUrl = path + '/static/print/print.html'
        } else {
          printUrl = '/static/print/print.html'
        }
        var page = window.open(printUrl, '')
        setTimeout(() => {
          page.document.body.innerHTML = printStr
          return false
        }, 50)
      })
    },
    changeOrderStatus(value) {
      this.query()
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.orderTimeBegin = timer[0]
      this.queryParams.orderTimeEnd = timer[1]
      this.query()
    },
    showAttr(data) {
      // 转换json字符串属性
      if (data === '') {
        return ''
      } else {
        const arr = JSON.parse(data)
        let str = ''
        arr.forEach(v => {
          const tmp = v.name + ':' + v.value + '  '
          str = str + tmp
        })
        return str
      }
    }
  },
  created() {
    this.getTableData(orderPage, this.queryParams)
  }
}
</script>

<style scoped lang='scss'>
</style>




